(function() {
  
  angular
    .module('bdApp')
    .controller('orderCtrl', orderCtrl);

  orderCtrl.$inject = ['$window', '$location', 'jean','popups', '$filter', 'aws', 'bdAPI', '$scope', 'jsonData'];
  function orderCtrl($window, $location, jean, popups, $filter, aws, bdAPI, $scope, jsonData) {
	  

	  
    var vm = this;
    vm.popups=popups.get();
		
		jean.setup();
		vm.jean = jean.get();
		
		vm.hasMeasurements = function(order) {
			console.log("HM Filter");
			var measurements = order.orderItems[0].jean.measurement;
			return Object.keys(measurements).length && measurements.waist;
		};
		
		var userDetails = aws.getCurrentUserFromLocalStorage();
		var identityId = bdAPI.setupHeaders(userDetails);

		if (vm.jean.jeanId && identityId){
			$location.path('/order/'+vm.jean.jeanId+'/'+identityId);
		}

		vm.orderForm={};
		vm.orderForm.step=1;
		
		vm.order = {
			"userId":identityId,
			"price":null,
			"shippingAddress":null,
			"vendor":null,
			"rep":null,
			"status":"New",
			"orderItems":[{
				"jean":vm.jean,
				"tracking":null,
				"notes":null,
			}],
			"timeline":null,
			"tailor":null,
		}
		
		vm.orders = [];
		bdAPI.call('ordersList', 100, function(result){
			console.log(result.data.items);
			for(i=0; i<result.data.items.length; i++){
				var item = result.data.items[i];
				if (item.userId == aws.getCurrentIdentityId() && item.status != "New"){
					vm.orders.push(item);
				}
			}
		});	

		vm.placeOrder = function(){			
			bdAPI.call('orderCreate', vm.order, function(result){
				vm.jean.ordered = true;
				bdAPI.call('jeansUpdate', [aws.getCurrentIdentityId(), vm.jean.jeanId, vm.jean], function(result){
					console.log("jeanSaved");
					console.log(result);					
				});
				bdAPI.call('commentsCreate', [result.data.orderId, {message:"Order Placed"}], function(comment){
					vm.popups.orderPlaced = true;
					jean.reset();
					vm.jean = jean.get();
					$scope.$apply();
				});
			});
		}

					
		vm.chooseFitType = function(type){
			vm.order.orderType = type;
			if (type == 2 && !vm.order.tailor){
				vm.popups.tailors=true;
			}	
		}
		
		vm.data=jsonData;
		
		vm.goToCloset = function(){
			console.log('closet');
			$location.path('/closet');
		}
		vm.startOver = function(){
			$location.path('/customizer');
		}
		
		vm.orderForm.nextStep = function(){
			
			//Step 1 to Step 2
			if (vm.orderForm.step==1){
				
				//Make sure we've got a fit type
				if (!vm.order.orderType){
					return false;
				}
				
				//If we're using a tailor, make sure we've got a tailor
				if (vm.order.orderType ==2 && !vm.order.tailor){
					vm.popups.tailors=true;
					return false;
				}
				
				vm.orderForm.step=2;
				return;
			}
			
		}


/*
		window.onbeforeunload = function() {
		    return 'You have unsaved changes!';
		}
	*/			
		
		
		
		function getPrimaryAddress(addresses){
			for (var i=0; i<addresses.length; i++) {
				if (addresses[i].primary == true) return addresses[i];
			}
			console.log('no address marked as primary... returning first result');
			return addresses[0];
		}
   
    var user = aws.getCurrentUserFromLocalStorage();
		
		
    if (user){
	    var identityID = bdAPI.setupHeaders(user);
			bdAPI.usersGet(identityID).then(
				function(result){
					vm.user = result.data;	
					vm.order.shippingAddress = getPrimaryAddress(vm.user.address);
					$scope.$apply();
				}, 
				function(err){console.log(err)} 
			);
    }else{
	    //redirect if not logged in
	    $location.path('/login');
		}	

		

	


		//Get User
  }
  



})();